import React from 'react';

import Slider from 'react-slick';
import SliderCard from './SliderCard';
import 'slick-carousel/slick/slick.css';

function BannerSlider() {
  const settings = {
    dots: false,
    infinite: true,
    speed: 500,
    slidesToShow: 3,
    slidesToScroll: 1,
    initialSlide: 0,
    arrows: true,
    autoplay: true,
    responsive: [
      {
        breakpoint: 1024,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 2,
          infinite: true,
        },
      },
      {
        breakpoint: 600,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
          infinite: true,
          arrows: false,
          dots: true,
        },
      },
    ],
  };
  return (
    <>

      <div className="section">
        <div className="container">
          <div className="row">
            <div className="col-md-12">
              <Slider {...settings} style={{ background: '#FFF' }}>
                <SliderCard image={`${process.env.PUBLIC_URL}/assets/images/frontend/surgical.png`} title="Surgical & Medical Equipment" />
                <SliderCard image={`${process.env.PUBLIC_URL}/assets/images/frontend/allopathy.png`} title="Allopathy + Generic" />
                <SliderCard image={`${process.env.PUBLIC_URL}/assets/images/frontend/ayurvedic.png`} title="Ayurvedic" />
                <SliderCard image={`${process.env.PUBLIC_URL}/assets/images/frontend/otc.png`} title="OTC" />
                <SliderCard image={`${process.env.PUBLIC_URL}/assets/images/frontend/veterinary.png`} title="Veterinary" />
              </Slider>
            </div>
          </div>
        </div>
      </div>

    </>
  );
}
export default BannerSlider;
