import useAuth from 'app/hooks/useAuth';
import { useNavigate, Link } from 'react-router-dom';

import "./../assets/css/animate.css";
import "./../assets/bootstrap/css/bootstrap.min.css";
import "./../assets/css/all.min.css";
import "./../assets/css/ionicons.min.css";
import "./../assets/css/themify-icons.css";
import "./../assets/css/linearicons.css";
import "./../assets/css/flaticon.css";
import "./../assets/css/simple-line-icons.css";

import "./../assets/css/magnific-popup.css";
import "./../assets/css/slick.css";
import "./../assets/css/slick-theme.css";

import "./../assets/css/style.css";
import "./../assets/css/custom.css";
import "./../assets/css/responsive.css";


const Header = () => {
  const { logout, user } = useAuth();
  let { isAuthenticated,
    // user
  } = useAuth();
  const navigate = useNavigate();

  return (
    <>
      <header className="header_wrap fixed-top header_with_topbar">
        <div className="bottom_header dark_skin main_menu_uppercase">
          <div className="container">
            <div className="row top-header">
              <div className="col-md-3">
                <Link className="navbar-brand" to="/">
                  <img className="logo_light" height="50px" src={`${process.env.PUBLIC_URL}/assets/images/frontend/Logo.png`} alt="logo" />
                  <img className="logo_dark" height="50px" src={`${process.env.PUBLIC_URL}/assets/images/frontend/Logo.png`} alt="logo" />
                </Link>
              </div>
              <div className="col-md-5">
                <ul className="social_icons social_white" style={{ margin: "20px auto 0px", textAlign: "center" }}>
                  <li>
                    <a href="https://www.facebook.com/Hegan.in" target="_blank" title='Facebook'>
                      <i className="ion-social-facebook"></i>
                    </a>
                  </li>
                  <li>
                    <a href="https://instagram.com/hegan.in" target="_blank" title='Instagram'>
                      <i className="ion-social-instagram-outline"></i>
                    </a>
                  </li>
                  <li>
                    <a href="https://twitter.com/Hegan_in" target="_blank" title='Twitter'>
                      <i className="ion-social-twitter"></i>
                    </a>
                  </li>
                  <li>
                    <a href="https://wa.me/message/YJFSS3YYS6ZYH1" target="_blank" title='WhatsApp'>
                      <i className="icon ion-social-whatsapp"></i>
                    </a>
                  </li>
                  <li>
                    <a href="https://www.linkedin.com/company/hegan/" target="_blank" title='LinkedIn'>
                      <i className="ion-social-linkedin"></i>
                    </a>
                  </li>

                </ul>
              </div>
              <div className="col-md-4">
                <ul className="navbarnav">
                  {isAuthenticated ? (
                    <>
                      <li><Link to="#" onClick={logout} title="Logout"><i className="fa fa-sign-out-alt"></i>Logout</Link></li>
                      <li><Link to="/dashboard" title="Dashboard"><i className="fa fa-user"></i>Dashboard</Link></li>
                    </>
                  ) : (
                    <>
                      <li><Link to="/login" title="Login"><i className="fas fa-sign-in-alt"></i>Login</Link></li>
                      <li><Link to="/partner-register" title="Register"><i className="fa fa-registered"></i>Register</Link></li>
                    </>
                  )}
                </ul>
              </div>
            </div>
          </div>
        </div>
      </header>
    </>
  );
};

export default Header;
